<template>
	<div id="store-house">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				进销对比
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--点击菜单（天数）-->
		<date-btns-nav @profitDateClick="profitDateClick"/>
		<!--滚动--->
		<scroll class="store-house-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<!--顶部菜单客户页面也用-->
		<purchase-and-sale-navs :psalepk-arr="psalepkArr"/>
		<!--排行列表-->
		<purchase-and-sale-content :psalepk-arr="psalepkArr"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import DateBtnsNav from 'components/common/datebtns/DateBtnsNav'


import PurchaseAndSaleNavs from 'components/common/balanceaccountsnavs/PurchaseAndSaleNavs'
import PurchaseAndSaleContent from './childComps/PurchaseAndSaleContent'


export default {
  name: 'PurchaseAndSalePk',
	components: {
		NavBar,
		Scroll,
		DateBtnsNav,
		PurchaseAndSaleContent,
		PurchaseAndSaleNavs
	},
	data(){
		return{
			currentType:'todayProfitArr'
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//今日 昨日点击按钮
		profitDateClick(index){
			//点击分类
			switch(index){
				case 0:
					this.currentType="todayProfitArr"
					break
				case 1:
					this.currentType="yesterdayProfitArr"
					break
				case 2:
					this.currentType="servedayProfitArr"
					break
				case 3:
					this.currentType="sanshidayProfitArr"
					break
			}
		},
	},
	computed:{
		psalepkArr(){
			return this.$store.state.psalepkArr[this.currentType]
		}
	}
}
</script >

